body {
  background-color: azure;
  background-image: url("https://img.freepik.com/free-vector/yellow-blue-wave-gradient-background-vector_53876-128354.jpg?w=1060&t=st=1685911978~exp=1685912578~hmac=9933ba395962ba3afe8b451c0abdcff9a23383fdbe0fd2d236c240c944a6cb5d");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-attachment: fixed;
  margin-top: 0;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 1600px;
  margin-left: auto;
  margin-right: auto;
}

.form-position {
  text-align: center;
  width: 100%;
  background-color: #54a4e5;
  border-radius: 5px;
  padding-top: 10px;
  padding-bottom: 10px;
  box-shadow: 0 4px 12px rgba(0, 0, 0, .2), 0 16px 20px rgba(249, 242, 32, .97);
}

.unvisible {
  display: none;
}

.gallery {
  width: 100%;
  grid-gap: 10px;
  grid-template-columns: repeat(4, 360px);
  justify-content: center;
  margin-top: 10px;
  display: grid;
}

.info {
  flex-direction: row;
  gap: 10px;
  display: flex;
}

.info-item {
  font-family: Times New Roman, Times, serif;
  font-size: medium;
  font-weight: 400;
}

.photo-card {
  text-align: center;
  background-color: #f5f5f5;
  border-radius: 5px;
  padding: 10px;
  box-shadow: 0 50px 100px -20px rgba(50, 50, 93, .25), 0 30px 60px -30px rgba(0, 0, 0, .3), inset 0 -2px 6px rgba(10, 37, 64, .35);
}

.photo-card img {
  max-width: 100%;
  height: auto;
}

.hover-image-scale {
  cursor: pointer;
  display: inline-block;
  overflow: hidden;
}

.hover-image-scale img {
  transition: all 1s;
  display: block;
}

.hover-image-scale img:hover {
  transform: scale(1.2);
}

.btn-submit {
  color: #050551;
  background-color: #57edfe;
  border: 0;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
  font-weight: 700;
  box-shadow: 0 50px 100px -20px rgba(50, 50, 93, .25), 0 30px 60px -30px rgba(0, 0, 0, .3), inset 0 -2px 6px rgba(10, 37, 64, .35);
}

.btn-submit:hover, .btn-submit:focus {
  color: #f5f5f5;
  background-color: #00f;
  transition: all 2s;
}

input {
  border-color: #00f;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
  box-shadow: 0 50px 100px -20px rgba(50, 50, 93, .25), 0 30px 60px -30px rgba(0, 0, 0, .3), inset 0 -2px 6px rgba(10, 37, 64, .35);
}

.load-more {
  color: #050551;
  cursor: pointer;
  background-color: #0ced14;
  border: 0;
  border-radius: 5px;
  margin-top: 10px;
  padding: 10px;
  font-size: 16px;
  font-weight: 700;
  box-shadow: 0 50px 100px -20px rgba(50, 50, 93, .25), 0 30px 60px -30px rgba(0, 0, 0, .3), inset 0 -2px 6px rgba(10, 37, 64, .35);
}

